import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { ServiceWrapper, ServiceItems } from "./style";
import { data } from "./data";
import PropTypes from "prop-types";
import Meta from "~/Component/Meta";
import Loading from "../Loading";
const ServiceItem = ({ serviceItem }) => {
  const { name, subInfo } = serviceItem;
  return (
    <ServiceItems>
      <h6 className="name-service">
        {name}

      </h6>
      <ul>
        {subInfo.map(item => <li key={item.id}>{item.name}</li>)}
      </ul>
    </ServiceItems>
  );

}
const Service = props => {
  const [service, setService] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    setIsLoading(true);
    setService(data);
    setTimeout(() => {
      setIsLoading(false)
    }, 2000);
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    })
  }, [])
  if (isLoading) return <>
    <Meta
      image="http://www.shoppanda.net/assets/images/banner/banner2.jpg"
      description='Bn bun, bn si thi trang phai tre tai Shop Panda
        Vi ngun hng n nh. c o, hng v hng thng vi s lng ln v hng trm mu m mi ko ng hng trn th trng.'
      url={`http://www.shoppanda.net/dich-vu/`}
      title="Dich vu SHOPPANDA"
      titlePage="Dch V"
    />
    <Loading />
  </>
  return (
    <ServiceWrapper className="service">

      <Meta
        image="http://www.shoppanda.net/assets/images/banner/banner2.jpg"
        description="Bn bun, bn si thi trang phai tre tai Shop Panda
        Vi ngun hng n nh. c o, hng v hng thng vi s lng ln v hng trm mu m mi ko ng hng trn th trng."
        url={`http://www.shoppanda.net/dich-vu/`}
        title="Dich vu SHOPPANDA"
        titlePage="Dch V"
      />
      <div className="container">
        <div className="header-page">
          <NavLink
            className="link-to"
            exact
            to="/"
          >
            Trang chu
          </NavLink>
          <span>/</span>
          <span>Cac dich vu cua ShopPanda</span>
        </div>
        <h2 className="title-service">
          Dich vu SHOPPANDA
        </h2>
        <p>
          Bn bun, bn si thi trang phai tre tai Shop Panda
        </p>
        <p>
          Vi ngun hng n nh. c o, hng v hng thng vi s lng ln v hng trm mu m mi ko ng hng trn th trng.
        </p>
        <p>
          Cc dng hng n nh bao gm:
        </p>
        <ul>
          {service.map(item => {
            return <ServiceItem serviceItem={item} key={item.id} />
          })}
        </ul>
      </div>
    </ServiceWrapper>
  );
}

ServiceItem.propTypes = {
  serviceItem: PropTypes.object
}

export default Service;